import React from 'react';

const imgStyle = {
    borderRadius: '20px 20px 0px 0px'
}

function Project(props) {

    return (
      <div className={`cardContainer ${props.id} posRel`}>
        <div className="card-inner">
          <div className="card-top">
            <img
              className="card-img"
              style={imgStyle}
              src={require(`../images/project-images/${props.img}`)}
              alt={props.name}
            />
          </div>
          <div className="card-bottom">
            <h3>{props.name}</h3>
            <p>{props.content}</p>
          </div>
        </div>
        <div className="card-links posBot">
          {props.projectUrl ? (
            <a
              className="icon fa-globe fa-3x"
              href={props.projectUrl}
              target="_blank"
              rel="noopener noreferrer"
            >
              <span className="label">Site</span>
            </a>
          ) : (
            <span></span>
          )}
          {props.githubUrl ? (
            <a
              className="icon fa-github fa-3x"
              href={props.githubUrl}
              target="_blank"
              rel="noopener noreferrer"
            >
              <span className="label">Github</span>
            </a>
          ) : (
            <span></span>
          )}
        </div>
      </div>
    )
}

export default Project;