import PropTypes from 'prop-types';
import React from 'react';
import projectList from './projectList';
import Project from '../Project';

class Main extends React.Component {

    constructor(props) {
        super(props);
        this.submitForm = this.submitForm.bind(this);
        this.state = {
            status: ""
        };
    }

    render() {
        const { status } = this.state;
        let close = (
          <div
            role="button"
            tabIndex="0"
            className="close"
            onClick={() => {
              this.props.onCloseArticle()
            }}
            onKeyDown={() => {
              this.props.onCloseArticle()
            }}
          ></div>
        )

        const cardContainerStyle = {
            position: 'relative',
            display: 'grid',
            gridTemplateColumns: 'repeat(auto-fit, minmax(18rem, 1fr))',
            gridGap: '1rem',
            justifyItems: 'center'
        }

        return (
          <div
            ref={this.props.setWrapperRef}
            id="main"
            style={
              this.props.timeout ? { display: 'flex' } : { display: 'none' }
            }
          >
            <article
              id="intro"
              className={`${this.props.article === 'intro' ? 'active' : ''} ${
                this.props.articleTimeout ? 'timeout' : ''
              }`}
              style={{ display: 'none' }}
            >
              <h2 className="major">Projects</h2>
              <div className="projectContainer" style={cardContainerStyle}>
                {projectList
                  .slice(0)
                  .reverse()
                  .map(project => (
                    <Project
                      key={project.id}
                      id={project.id}
                      content={project.content}
                      img={project.img}
                      name={project.name}
                      githubUrl={project.githubUrl}
                      projectUrl={project.projectUrl}
                    />
                  ))}
              </div>
              {close}
            </article>

            <article
              id="work"
              className={`${this.props.article === 'work' ? 'active' : ''} ${
                this.props.articleTimeout ? 'timeout' : ''
              }`}
              style={{ display: 'none' }}
            >
              <h2 className="major">About Me</h2>
              <p>
                Adipiscing magna sed dolor elit. Praesent eleifend dignissim
                arcu, at eleifend sapien imperdiet ac. Aliquam erat volutpat.
                Praesent urna nisi, fringila lorem et vehicula lacinia quam.
                Integer sollicitudin mauris nec lorem luctus ultrices.
              </p>
              <p>
                Nullam et orci eu lorem consequat tincidunt vivamus et sagittis
                libero. Mauris aliquet magna magna sed nunc rhoncus pharetra.
                Pellentesque condimentum sem. In efficitur ligula tate urna.
                Maecenas laoreet massa vel lacinia pellentesque lorem ipsum
                dolor. Nullam et orci eu lorem consequat tincidunt. Vivamus et
                sagittis libero. Mauris aliquet magna magna sed nunc rhoncus
                amet feugiat tempus.
              </p>
              {close}
            </article>

            <article
              id="contact"
              className={`${this.props.article === 'contact' ? 'active' : ''} ${
                this.props.articleTimeout ? 'timeout' : ''
              }`}
              style={{ display: 'none' }}
            >
              <h2 className="major">Contact</h2>
              <form
                method="post"
                action="https://formspree.io/xyywdavl"
                onSubmit={this.submitForm}
              >
                <div className="field half first">
                  <label htmlFor="name">Name</label>
                  <input type="text" name="name" id="name" />
                </div>
                <div className="field half">
                  <label htmlFor="email">Email</label>
                  <input type="email" name="email" id="email" />
                </div>
                <div className="field">
                  <label htmlFor="message">Message</label>
                  <textarea name="message" id="message" rows="4"></textarea>
                </div>
                <ul className="actions">
                  <li>
                    {status === 'SUCCESS' ? (
                      <p>Message Sent!</p>
                    ) : (
                      <button>Submit</button>
                    )}
                    {status === 'ERROR' && <p>Ooops! There was an error.</p>}
                    {/* <input type="submit" value="Send Message" className="special" /> */}
                  </li>
                  <li>
                    <input type="reset" value="Reset" />
                  </li>
                </ul>
              </form>
              <ul className="icons">
                <li>
                  <a
                    href="https://www.linkedin.com/in/rgonz166/"
                    className="icon fa-linkedin"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <span className="label">LinkedIn</span>
                  </a>
                </li>
                <li>
                  <a
                    href="https://github.com/rgonz166/"
                    className="icon fa-github"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <span className="label">GitHub</span>
                  </a>
                </li>
                <li>
                  <a
                    href="mailto:raulgcoding@gmail.com"
                    className="icon fa-envelope"
                  >
                    <span className="label">E-mail</span>
                  </a>
                </li>
              </ul>
              Or e-mail
              <span style={{ paddingLeft: '5px' }}>
                <a href="mailto:raulgcoding@gmail.com">raulgcoding@gmail.com</a>
              </span>
              {close}
            </article>

            <article
              id="about"
              className={`${this.props.article === 'about' ? 'active' : ''} ${
                this.props.articleTimeout ? 'timeout' : ''
              }`}
              style={{ display: 'none' }}
            >
              <h2 className="major">Resume</h2>
              <a
                href={require('../../images/RGmyResume.pdf')}
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={require('../../images/project-images/myResume.png')}
                  style={{ width: '100%' }}
                  alt="Resume"
                />
              </a>

              {close}
            </article>
          </div>
        )
    }
    // email formspree 
    submitForm(ev) {
        ev.preventDefault();
        const form = ev.target;
        const data = new FormData(form);
        const xhr = new XMLHttpRequest();
        xhr.open(form.method, form.action);
        xhr.setRequestHeader("Accept", "application/json");
        xhr.onreadystatechange = () => {
            if (xhr.readyState !== XMLHttpRequest.DONE) return;
            if (xhr.status === 200) {
                form.reset();
                this.setState({ status: "SUCCESS" });
            } else {
                this.setState({ status: "ERROR" });
            }
        };
        xhr.send(data);
    }
}

Main.propTypes = {
    route: PropTypes.object,
    article: PropTypes.string,
    articleTimeout: PropTypes.bool,
    onCloseArticle: PropTypes.func,
    timeout: PropTypes.bool,
    setWrapperRef: PropTypes.func.isRequired,
}

export default Main
